import React from 'react';
// import { useIntl } from 'react-intl';
import {
  ProSidebar,
  Menu,
  MenuItem,
  SubMenu,
  SidebarHeader,
  SidebarFooter,
  SidebarContent,
} from 'react-pro-sidebar';

import {
  SiLine,
} from 'react-icons/si';
import {
  FcPlanner,
  FcAssistant,
  FcRating,
  FcGallery,
} from 'react-icons/fc';

import {
  Common,
} from '@Config';

// import sidebarBg from '@Assets/bg1.jpg';

import { useTranslation } from 'react-i18next';
import { tw } from 'twind';

/**
 * SideBar
 *
 * @param {*} param0
 * @returns
 */
const SideBar = ({ image, collapsed, rtl, toggled, handleToggleSidebar }) => {
  const { t } = useTranslation();

  return (
    <ProSidebar
      // image={image ? sidebarBg : false}
      // image={sidebarBg}
      rtl={rtl}
      collapsed={collapsed}
      toggled={toggled}
      breakPoint="md"
      onToggle={handleToggleSidebar}
      className={(toggled) ? 'sidebar-container' : ''}
    >
      <SidebarHeader>
        <div
          className={tw`h-16`}
          style={{
            // padding: '24px',
            textTransform: 'uppercase',
            fontWeight: 'bold',
            fontSize: 14,
            letterSpacing: '1px',
            overflow: 'hidden',
            textOverflow: 'ellipsis',
            whiteSpace: 'nowrap',
          }}
        >
          { `放 Logo` }
        </div>
      </SidebarHeader>

      <SidebarContent>
        <Menu iconShape="circle">
          <MenuItem
            icon={<FcRating />}
            // suffix={<span className="badge red">{ `隨便放` }</span>}
          >
            { t('menus.index') }
          </MenuItem>
          <SubMenu
            // suffix={<span className="badge yellow">3</span>}
            title={ t('menus.service_and_promise') }
            icon={<FcAssistant />}
          >
            <MenuItem>{ t('menus.working_holiday_visa_2') }</MenuItem>
            <MenuItem>{ t('menus.working_holiday_visa_3')}</MenuItem>
            <MenuItem>{ t('menus.student_visa')}</MenuItem>
            <MenuItem>{ t('menus.invest_and_migration')}</MenuItem>
          </SubMenu>
          <MenuItem
            icon={<FcGallery />}
          >
            { t('menus.experience_sharing') }
          </MenuItem>
          <MenuItem
            icon={<FcPlanner />}
            // suffix={<span className="badge red">{ `隨便放` }</span>}
          >
            { t('menus.plan_of_australia') }
          </MenuItem>
        </Menu>
      </SidebarContent>

      <SidebarFooter style={{ textAlign: 'center' }}>
        <div
          className="sidebar-btn-wrapper"
          style={{
            padding: '10px 24px',
          }}
        >
          <a
            href={`https://line.me/ti/p/${Common.LINE_OFFICAL_ID}`}
            target="_blank"
            className="sidebar-btn"
            rel="noopener noreferrer"
          >
            <SiLine />
            <span> {t('contact')}</span>
          </a>
        </div>
      </SidebarFooter>
    </ProSidebar>
  );
};

export default SideBar;
